@import '../../u1-assets/mixins/mixins';

.topBar {
   @include display(flex, $a-items: center);
   z-index: 1;
   background: #34465d;
   height: 66px;
   padding: 0 16px;
   position: fixed;
   top: 0;
   width: 100%;
   box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);

   .logoBlock {
      @include display(flex, $a-items: center);
      width: 20%;

      .logoLink {
         display: flex;

         .logoName {
            @include display(flex, $a-items: center);
            color: white;
            font-size: 16px;
            margin: 0;
         }

         .logoImg {
            width: 50px;
            padding: 0 5px;
         }
      }
   }

   .topArea {
      @include display(flex, flex-end, center);
      height: 100%;
      width: 80%;

      .pageNameBlock {
         @include display(flex, $a-items: center);
         height: 100%;
         padding-right: 20px;
         border-right: 1px solid rgba(255, 255, 255, 0.1);

         .pageName {
            color: #fff;
            font-size: 14px;
            text-transform: uppercase;
         }
      }

      .userImg {
         @include display(flex, $a-items: center);
         height: 100%;
         padding-left: 30px;
         color: #fff;

         .isAuthState {
            display: inline-block;
            font-size: 14px;
            font-weight: 500;
            margin: 0 10px 0 0;
         }

         .userPhoto {
            display: inline-block;
            width: 45px;
            height: 45px;
            border-radius: 50%;
            border: 2px solid rgba(255, 255, 255, .8);
            cursor: pointer;
         }

         .login {
            color: #fff;
            font-size: 14px;
            text-transform: uppercase;
         }
      }

      .loginBtn {
         padding: 0 30px;

         a {
            img {
               width: 20px;
            }
         }
      }
   }
}