@import '../../u1-assets/mixins/mixins';

.mainBlockWrapper {
   .row {
      display: flex;

      .userProfile {
         display: inline-block;
         width: 100%;
         margin: 100px 0 30px 0;

         .main {
            @include display(flex, $a-items: flex-end);
            background-image: url("../../u1-assets/images/main.jpg");
            background-position: center;
            background-size: cover;
            border-radius: 5px 5px 0 0;
            height: 370px;
            padding-bottom: 10px;

            .profileAuthor {
               @include display(flex, $a-items: center, $f-der: column);
               padding: 0 6%;
               margin-left: 10px;
               text-align: center;
               width: 15%;

               .profileImgBlock {
                  position: relative;
                  border-radius: 50%;

                  &:hover {
                     .imageUpload {
                        display: block;
                     }
                  }

                  .authorImg {
                     border: 3px solid #fff;
                     border-radius: 50%;
                     height: 100%;
                     width: 100%;
                  }

                  .imageUpload {
                     display: none;
                     height: 40px;
                     width: 40px;
                     background: rgba(255, 255, 255, 0.7) none no-repeat scroll center;
                     background-size: 50%;
                     border: 1px solid #b3b3b3;
                     border-radius: 50%;
                     @include position(absolute, $r: 10px, $b: 0);

                     .inputLabel {
                        @include display(flex, center, center);
                        background-image: url("../../u1-assets/images/main/camera.png");
                        background-size: inherit;
                        background-position: center;
                        background-repeat: no-repeat;
                        height: 100%;
                        width: 100%;

                        .photoLoad {
                           cursor: pointer;
                           display: none;
                           width: 100%;
                        }
                     }
                  }
               }
            }
         }

         .profileNavSection {
            @include display(flex, $a-items: center);
            letter-spacing: 0.2px;
            background: #ffffff;
            height: 65px;
            padding: 0 15px;

            .navTabs {
               @include display(flex, $a-items: center);
               width: 100%;

               .authorContent {
                  @include display(flex, center, center, $f-der: column);
                  padding: 0 15px;
                  font-size: 20px;
                  font-weight: 500;
                  width: 25%;

                  .userName {
                     @include display(flex, center);
                     color: #333;
                     margin-bottom: .3rem;
                  }

                  .country {
                     font-size: 12px;
                     font-weight: 400;
                     color: #757a95;
                  }
               }
            }

            .navLinks {
               display: flex;
               padding: 0 15px;

               li {
                  margin-bottom: 0;
                  margin-right: 50px;

                  &:last-child {
                     margin-right: 0;
                  }

                  &.active {
                     color: #fa6342;
                  }

                  a {
                     font-size: 14px;
                     cursor: pointer;
                     font-weight: 500;
                     display: inline-block;

                     &.active {
                        color: #fa6342;
                     }
                  }
               }
            }
         }
      }
   }
}

